import React, { Component } from 'react';
import './App.css';
import * as firebase from 'firebase';
import Datetime from 'react-datetime';

class DateForm extends Component {
    constructor() {
      super();
      this.state = {
        date: "",
        eventName: "",
      };

      this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event) {
      const target = event.target;
      const value = target.value;
      const name = target.name;
      this.setState({
        [name]: value
      });
    }

    setDate(date) {
      this.setState({
        date: date.valueOf()
      })
    }

    addDate() {
      const countdownsRef = this.props.db.child('countdowns');
      const newCountdownRef = countdownsRef.push();
      newCountdownRef.set({
        date: this.state.date,
        eventName: this.state.eventName
      });

      newCountdownRef.on('value', snap => {
        this.setState({
          date: "",
          eventName: "",
        });
      });
    }

    render() {
      return (
        <form className="DateForm">
          <input type="text" value={this.state.eventName} name="eventName" onChange={this.handleChange} placeholder="Enter Event Name"/>
          <Datetime value={this.state.date} onChange={(date) => this.setDate(date)} inputProps={{placeholder: "Select a Date"}}/>
          <button type="button" onClick={() => this.addDate()}>Add</button>
        </form>
      )
    }
}

class App extends Component {

  constructor() {
    super();
    this.state = {
      rightNow: new Date(),
      until: new Date("2017-02-14T00:00:00-08:00"),
      db: firebase.database().ref('countdowns/')
    };
    fetch('/api/food', (res) => {
      console.log(res);
    });
  }

  componentDidMount() {
    setInterval(function() {
        this.setState({
            rightNow: new Date()
        });
    }.bind(this), 1000);
  }

  render() {
    let timeUntil = this.state.until.getTime()- this.state.rightNow.getTime();
    const second = 1000;
    const minute = 60 * second;
    const hour = 60 * minute;
    const day = 24 * hour;
    const days = convertToStringAndCut(timeUntil / day);
    const hours = convertToStringAndCut((timeUntil % day)/ hour);
    const minutes = convertToStringAndCut(((timeUntil % day) % hour) / minute);
    const seconds = convertToStringAndCut((((timeUntil % day) % hour) % minute) / second);
    return (
      <div className="App">
        {/* <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h2>Welcome to React</h2>
        </div>
        <p className="App-intro">

        </p> */}
        <h2>{this.state.until.toDateString()}</h2>
        <h3>{days} Days {hours} Hours {minutes} Minutes {seconds} seconds</h3>
        <DateForm db={this.state.db}/>
      </div>
    );
  }
}

function convertToStringAndCut(time) {
  const timeStr = time.toString();
  return timeStr.substr(0, timeStr.indexOf('.'));
}

export default App;



